var graceChecker = require("../../graceUI/graceChecker.js");
import LongButton from '../../components/my-components/LongButton';
let requesting = false;
let canLogin = false; //可否登录 
export default {
	components: {
		LongButton
	},
	data() {
		return {
			openEye: false,
			userId: '',
			password: '',
			idError: false, //用户信息错误
		}
	},
	computed: {
		disableButton() {
			this.idError = false;
			return !this.userId || !this.password;
		}
	}, 
	methods: {
		initPage() {
			this.openEye = false;
			canLogin = false;
			this.userId = '';
			this.password = '';
			this.idError = false;
		},
		getUserId(val) {
			this.userId = val.detail;
		},
		getPassWord(val) {
			this.password = val.detail;
		},
		clearInput(key) {
			this[key] = '';
		},
		clickPWIcon() { //切换加密显示与明文显示
			this.openEye = !this.openEye;
		},
		loginWithWx: function() {
			this.closePageLoading();
			this.Toast('当前微信暂未绑定任何管理员账号');
		},
		loginNow: function(e) {
			if (!requesting) { //函数节流
				requesting = true; //是否正在请求接口
				this.pageLoading();
				setTimeout(() => {
					// 验证通过
					canLogin = true;
					requesting = false;
					if (canLogin) {
						uni.reLaunch({
							url: '../../pagesLogin/pages/selectShop'
						})
					} else {
						this.idError = true;
					}
					this.closePageLoading();
					requesting = false;
				}, 1000)
			}
		},
		reg: function() { //找回密码
			uni.navigateTo({
				url: '../../pagesLogin/pages/index'
			})
		}
	}
}
